<template>
  
  <main class="bg-black">
    <div class="bg" :style="`background-image:url(${musics[num].photo});`"></div>
    <div class="base">
      <div class="info">
        <h2 class="mb-4">{{musics[num].singer}}</h2>
        <h3 class="mb-5">{{musics[num].song}}</h3>
        <div class="time">
          <span>{{currentTime}}</span>
          <span>{{durationTime}}</span>
        </div>
        <div class="control">
          <svg  @click="previousBtn"   xmlns="http://www.w3.org/2000/svg" width="35" height="35" fill="currentColor" class="bi bi-skip-backward-circle" viewBox="0 0 16 16">
