            <path d="M8 15A7 7 0 1 1 8 1a7 7 0 0 1 0 14zm0 1A8 8 0 1 0 8 0a8 8 0 0 0 0 16z"/>
            <path d="M4.271 5.055a.5.5 0 0 1 .52.038L7.5 7.028V5.5a.5.5 0 0 1 .79-.407L11 7.028V5.5a.5.5 0 0 1 1 0v5a.5.5 0 0 1-1 0V8.972l-2.71 1.935a.5.5 0 0 1-.79-.407V8.972l-2.71 1.935A.5.5 0 0 1 4 10.5v-5a.5.5 0 0 1 .271-.445z"/>
          </svg>
          
        </div>
        <div class="photo" :style="`background-image: url(${musics[num].photo});`"></div>
        <div :class="diskStatus" ><div class="innerdisk"  :style="`background-image: url(${musics[num].photo});`"></div></div>
      </div>
      
    </div>
  </main>
  <HamMenu  @playlist="playList"/>

   

</template>



<script>
import { ref } from "vue";
import musics from "./musics.js";
import HamMenu   from "./components/HamMenu.vue"
export default {
    name: "App",
    components:{HamMenu},
    setup() {
      const num =ref(0);
      const status =ref(false);
      const diskStatus =ref('disk');
      const audio =ref(null);
      const durationTime =ref('');
      const currentTime =ref('');
  
      

      audio.value = new Audio();
      audio.value.src = musics[num.value].source;


      const playBtn = () =>{
          if(!status.value){
          diskStatus.value = 'playdisk';
          audio.value.play();
          status.value = true;
        }else{
          diskStatus.value = 'disk';
          audio.value.pause();
          status.value = false;
        }
      };

      const nextBtn = () =>{
          if(status.value){
               playBtn();
               if(num.value<musics.length-1){
                   num.value+=1;
                   audio.value.src = musics[num.value].source;
                   setTimeout(() => {
                     playBtn()
                   }, 1000);
               }else{
                   num.value = 0;
                   audio.value.src = musics[num.value].source;
                   setTimeout(() => {
                     playBtn()
                   }, 1000);
               }

          }else{
               if(num.value<musics.length-1){
                   num.value+=1;
                   audio.value.src = musics[num.value].source;
                   playBtn();
               }else{
                   num.value = 0;
                   audio.value.src = musics[num.value].source;
                   playBtn();
               }

          }
              
          
      };

      const previousBtn = () =>{
          if(status.value){
               playBtn();
               if(num.value>0){
                   num.value-=1;
                   audio.value.src = musics[num.value].source;
                   setTimeout(() => {
                     playBtn()
                   }, 1000);
               }else{
                   num.value = musics.length-1;
                   audio.value.src = musics[num.value].source;
                   setTimeout(() => {
                     playBtn()
                   }, 1000);
               }

          }else{
               if(num.value>0){
                   num.value-=1;
                   audio.value.src = musics[num.value].source;
                   playBtn();
               }else{
                   num.value = musics.length-1;
                   audio.value.src = musics[num.value].source;
                   playBtn();
               }

          }
      };

      const generateTime = () => {
      let durMin = Math.floor(audio.value.duration / 60);
      let durSec = Math.floor(audio.value.duration - durMin * 60);

      let curMin = Math.floor(audio.value.currentTime / 60);
      let curSec = Math.floor(audio.value.currentTime - curMin * 60);
      
      
      if(durMin < 10) {
        durMin = '0' + durMin;
      }
      if(durSec < 10) {
        durSec = '0' + durSec;
      }
      if(curMin < 10) {
        curMin = '0' + curMin;
      }
      if(curSec < 10) {
        curSec = '0' + curSec;
      }
      durationTime.value = `${durMin}:${durSec}`;
      currentTime.value = `${curMin}:${curSec}`;
      if(durationTime.value==currentTime.value){
        nextBtn();
      }
      };

      audio.value.onloadedmetadata = ()=>{
          generateTime();
      };

      audio.value.ontimeupdate = ()=>{
          generateTime();  
      };

      const playList = (index)=>{
          status.value = true;
          playBtn();
          num.value=index;
          audio.value.src = musics[num.value].source;
          setTimeout(() => {playBtn()}, 1000);
      };

      return{
        musics,
        num,
        status,
        diskStatus,
        audio,
        durationTime,
        currentTime,
        playBtn,
        nextBtn,
        previousBtn,
        generateTime,
        playList
      }
    }   
} 
</script>


