<template>
  <i class="bi bi-list text-light ham"  @click="showMenu"></i>
  <div :class="menuStatus">
    
    <ol>
        <li class="fs-5 fw-bold  mb-4">لیست آهنگ ها</li>
        <li v-for="(item,index) in musics" :key="item" @click="$emit('playlist',index)" ><span>{{item.song}}</span> - {{item.singer}}</li>
    </ol>
    
    <i class="bi bi-x-lg close" @click="closeMenu"></i>
  </div>


  
</template>

<script>
import { ref,onMounted } from "vue";
import musics from "../musics";
export default {
    name:"HamMenu",
    
    setup(){
        
        const menuStatus = ref('menu');
        const currentMusic = ref(null);
        
          
        
        const showMenu=()=>{
          menuStatus.value='menuafter';
        };

        const closeMenu=()=>{
          menuStatus.value='menu';
        };

        onMounted(() => {
          let a = 'red';
          document.getElementsByTagName('li').style.color=a;
        });

      return{
          menuStatus,
          showMenu,
          closeMenu,
          musics,
          currentMusic
          
      }
    }

}
</script>

<style lang="scss" scoped>
@import url('../assets/fonts.scss');
*{
 overflow: hidden;
}
.menu{
  background-color: rgb(255, 255, 255);
  width: 300px;
  height: 100vh;
  transform: translateX(-350px);
  transition: all 0.4s;

}
.menuafter{
  background-color: white;
  width: 300px;
  height: 100vh;
  
  transform: translateX(0px);
  transition: all 0.5s;
}
.close{
  position: absolute;
  top: 5px;
  right: 5px;
  font-size: 1.5rem;
  cursor: pointer;
}
.ham{
  position: fixed;
  top: 0;
  left: 0;
  font-size: 3rem;
  margin: 10px;
  cursor: pointer;
  transition: all 0.4s;
  &:hover{
    color: rgb(175, 70, 58)!important;
  }
}
ol{
  margin-top:5rem;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  
  li{
    font-family: yekan;
    margin: 10px 20px;
    cursor: pointer;
    &:visited{
      color: aqua;
    }
  }
  span{
    font-family: yekan!important;
    color:rgb(148, 26, 17) ;
  }
}


@media screen and (max-width:933px){
  .menu{
  background-color: rgb(255, 255, 255);
  width: 300px;
  height: 100vh;
  transform: translateX(-350px);
  transition: all 0.4s;

}
.menuafter{
  background-color: white;
  width: 300px;
  height: 100vh;
  
  transform: translateX(0px);
  transition: all 0.5s;
}
}
</style>